    left: 0;
    border-bottom: 1px solid black;
    box-sizing: content-box;
    pointer-events: none; }
  .banner__heading {
    font-size: 3rem;
    font-weight: normal;
    color: #e6e6e6;
    align-self: center;
    margin: auto;
    letter-spacing: 1px;
    padding-right: 8rem; }
    @media (max-width: 56.25em) {
