        padding-right: 0; } }
    @media (max-width: 37.5em) {
      .banner__heading {
        font-size: 2.2rem; } }

.tab-nav {
  margin-left: 3%; }
  .tab-nav__list {
    list-style-type: none;
    display: flex; }
  .tab-nav__item {
    margin: 0 2px; }
  .tab-nav__item--active {
    z-index: 20; }
  .tab-nav__btn {
    color: white;
    border-radius: 6px 6px 0 0;
    padding: 10px 12px;
    border: 1px solid black;
    border-bottom: none;
    cursor: pointer;
    background-color: #404040;
    white-space: nowrap; }
    @media (max-width: 37.5em) {
      .tab-nav__btn {
        padding: 6px 12px; } }
    .tab-nav__btn:focus {
      outline: none; }
    .tab-nav__btn--active {
      border: 1px solid #191919;
      border-bottom: none;
      box-shadow: 1px -1px 2px rgba(0, 0, 0, 0.5);
      background-color: #4e4e4e;
      background-image: url("./brushed_metal_dark_med_hd.jpg"); }

.settings-form {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  color: #e6e6e6;
  font-size: 16px; }
  .settings-form *:focus {
    outline: 1px solid orangered; }
  .settings-form__group {
    display: flex;
    flex-direction: column;
    max-width: 40%;
    min-width: 275px;
    margin-right: 3%; }
    @media (max-width: 56.25em) {
      .settings-form__group {
        max-width: none;
        width: 100%;
        margin-right: 0; } }

.fieldset {
  position: relative;
  border: 2px solid #333333;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 0.2em 0.4em 0.5em; }
  .fieldset:last-child {
    margin-bottom: 0; }
  .fieldset__legend {
    margin-left: 2%; }
    .fieldset__legend--accordion {
      width: 96%;
      margin: 0 auto; }
    .fieldset__legend-flexwrap {
      display: flex;
      justify-content: space-between;
      align-items: center; }
    .fieldset__legend-hr {
      flex: 1;
      border: none;
      border-bottom: 2px solid #333333; }
  .fieldset__heading {
    font-size: 18px;
    font-weight: normal;
    margin: 0 6px;
    color: #e6e6e6;
    letter-spacing: 0.8px; }
  .fieldset__accordion-btn {
    background-color: transparent;
    font-size: 18px;
    width: 24px;
    height: 24px;
    margin: 0 6px;
    color: #e6e6e6;
    border-radius: 4px;
    border: 1px solid #333333;
    cursor: pointer; }
  .fieldset__accordion-content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 2%;
    max-height: 500px;
    transition: max-height 0.3s ease-in, opacity 0.4s;
    overflow: hidden; }
    .fieldset__accordion-content--collapsed {
      transition: max-height 0.3s, opacity 0.2s 0.1s;
      max-height: 0 !important;
      opacity: 0 !important; }
  .fieldset__group {
    display: inherit;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -14px;
    margin-left: -22px;
    padding: 14px 0; }
    @media (max-width: 37.5em) {
