        margin-left: -18px; } }
    .fieldset__group--space {
      justify-content: space-between; }
  .fieldset__divider {
    border: none;
    border-top: 1px solid #333333; }
  .fieldset__description {
    font-size: 14px;
    font-style: italic; }

.preview {
  flex: 1;
  margin: 0; }
  @media (max-width: 56.25em) {
    .preview {
      flex: none;
      width: 100%;
      margin-bottom: 20px; } }
  .preview__wrapper {
    margin: 24px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; }
  .preview__sub-heading {
    font-size: 16px;
    font-weight: normal;
    padding: 0 3%; }
  .preview__search-form {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85%;
    min-width: 48vw;
    padding: 10px 3%;
    flex-wrap: wrap; }
    @media (max-width: 56.25em) {
      .preview__search-form {
        width: 95%; } }
  .preview__search-input {
    width: 100%;
    max-width: 250px;
    padding: 2px;
    margin: 3% 4%; }
  .preview__submit-btn {
    padding: 4px 12px;
