    margin-left: 4px;
    align-self: stretch; }

.checkbox-field {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: darkgray;
  border: solid 1px #333333;
  box-shadow: inset 1px 1px 2px #282828;
  padding: 0;
  margin: 0; }
  .checkbox-field__label {
    align-self: center;
    display: flex;
    white-space: nowrap; }
  .checkbox-field__wrapper {
    position: relative;
    width: 16px;
    height: 16px;
    margin-right: 6px; }
  .checkbox-field__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0; }
  .checkbox-field::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    color: black;
    font-weight: bold;
    width: 35%;
    height: 70%;
    border-right: solid 2px #333333;
    border-bottom: solid 2px #333333;
    transform: translate(-40%, -52%) rotate(45deg); }
  .checkbox-field__wrapper input:checked ~ .checkbox-field {
    background-color: #e6e6e6; }
  .checkbox-field__wrapper input[type="checkbox"]:checked ~ .checkbox-field::after {
    opacity: 1; }

.number-field--sm {
  width: 44px; }

.number-field--md {
  width: 52px; }

.number-field--lg {
  width: 60px; }

.number-field--xl {
  width: 66px;
  padding-left: 2px; }

.text-field {
  width: 100%;
  padding: 2px; }

.color-field {
