  flex-direction: column;
  max-width: 100%; }
  .source-code__view {
    flex-direction: column;
    align-items: center;
    font-size: 16px; }
  .source-code__block {
    position: relative;
    background-color: #c3c3c3;
    border: 2px solid #333333;
    border-radius: 4px;
    margin: 0 40px 80px;
    padding: 20px;
    max-height: 500px;
    overflow: auto;
    tab-size: 4; }
    @media (max-width: 37.5em) {
