        margin: 0 0 40px; } }
  .source-code__heading {
    font-size: 18px;
    font-weight: normal;
    margin-left: 54px;
    padding: 0 6px;
    color: #e6e6e6; }
    @media (max-width: 37.5em) {
      .source-code__heading {
        margin-left: 0; } }
